import React, { useEffect, useRef, useState } from "react";
import apple from "../assets/apple.png";
import mango from "../assets/mango.png";
import orange from "../assets/orange.png";
import gif1 from "../assets/gif.gif";
import { motion } from "framer-motion";
export default function SlotGame() {
  const ref = useRef(null);
  const ref1 = useRef(null);
  const ref2 = useRef(null);
  const ref3 = useRef(null);
  const Array = [apple, mango, orange, gif1];

  const [value, addValue] = useState({
    val1: mango,
    val2: mango,
    val3: orange,
  });
  const [result, setResult] = useState("");
  const checkvalue = () => {
    setTimeout(() => {
      addValue({
        val1: Array[Math.floor(Math.random() * 3)],

        val2: Array[Math.floor(Math.random() * 3)],
        val3: Array[Math.floor(Math.random() * 3)],
      });
    }, 1000);
    addValue({
      val1: Array[3],
      val2: Array[3],
      val3: Array[3],
    });
    setResult("loading");
  };

  useEffect(() => {
    if (value.val1 === value.val2 && value.val2 === value.val3) {
      setResult(true);
    } else setResult(false);
  }, [checkvalue]);

  // spin

  return (
    <>
      <div className="slot">
        <div ref={ref} className="container text-center">
          <motion.h1 drag dragConstraints={ref} className="heading">
            Slot game
          </motion.h1>
          <div className="row mt-5">
            <div ref={ref1} className="col-md-4">
              <motion.img
                drag
                dragConstraints={ref1}
                src={value.val1}
                style={{ width: "10rem", height: "10rem" }}
                alt=""
              />
            </div>
            <div ref={ref2} className="col-md-4">
              <motion.img
                drag
                dragConstraints={ref2}
                src={value.val2}
                style={{ width: "10rem", height: "10rem" }}
                alt=""
              />
            </div>
            <div ref={ref3} className="col-md-4">
              <motion.img
                drag
                dragConstraints={ref3}
                src={value.val3}
                style={{ width: "10rem", height: "10rem" }}
                alt=""
              />
            </div>
          </div>

          <button className="btn btn-primary mt-5" onClick={checkvalue}>
            PLAY
          </button>
          {/* <h1 className="fw-b mt-5">{result}</h1> */}
          {result && <h1 className="fw-b mt-5 moving-text">you win</h1>}
        </div>
      </div>
    </>
  );
}

